

.galeria {

    padding: $separacion 0;

    h3 {
        text-align: center;
    }
}

.galeria-imagenes {
    padding: 0;
    margin-top: $separacion;
    list-style: none;

    @include grid(2, $separacion);

    @include responsive-tab {
        @include grid(4, $separacion / 2);
    }

    @include responsive-desk {
        @include grid(6, $separacion / 2);
    }
}

.imagen-zoom {
    width: 40%;
    height: auto;
    z-index: 1;
}

.transition {
    transition: all .5s ease;
}

.overlay {
    display: grid;
    place-items: center;
    position: fixed;
    background-color: rgba(0,0,0,.5);
    width: 100%;
    height: 100%;
    top: 0;
}

.btn-cerrar {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 5rem;
    height: 5rem;
    background-color: $white;
    border-radius: 50%;
    font-weight: $bold;

    &:hover {
        cursor: pointer;
        @include btn-darker($white, 8);
    }
}