
.video {

    position: relative;
    height: 70rem;
    overflow: hidden;

    .overlay {
        position: absolute;
        width: 100%;
        height: 100%;
        /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#e52dd0+0,9e37f2+63,ffffff+100&0.55+0,0.55+100 */
        background: -moz-linear-gradient(-45deg, rgba(229,45,208,0.55) 0%, rgba(158,55,242,0.55) 63%, rgba(255,255,255,0.55) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(-45deg, rgba(229,45,208,0.55) 0%,rgba(158,55,242,0.55) 63%,rgba(255,255,255,0.55) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(135deg, rgba(229,45,208,0.55) 0%,rgba(158,55,242,0.55) 63%,rgba(255,255,255,0.55) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#8ce52dd0', endColorstr='#8cffffff',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
    }

    video {
        display: block;
        width: 100%;
    }
}

.contenido-video {
    display: flex;
    align-items: center;
    height: 100%;
    position: relative;
    z-index: 1;

    h2 {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        font-size: 6.5rem;
        color: $white;
    }

    span {
        background-color: $rosa;
        padding: .9rem 1.5rem;
        font-weight: $regular;
        font-size: 2.8rem;
    }
}
