

.header {
    // width: 100%;
    background-color: $verde;
    // position: fixed;
    // z-index: 10;

    .contenido-header {
        padding: $separacion / 2 0;

        @include responsive-mob {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }

    .titulo {
        font-size: 4rem;
        font-weight: $bold;
        color: $white;
        margin-bottom: 1rem;
        text-align: center;

        @include responsive-tab {
            margin-bottom: 0;
        }
    }

    &.fijo {
        position: fixed;
        width: 100%;
        box-shadow: 0 .3rem .4rem #444;
    }
}

.navegacion-principal {
    
    display: flex;
    justify-content: center;
    gap: 2rem;

    a {
        color: $white;
        font-size: 2rem;
        // margin-right: 2rem;

        &:hover {
            text-decoration: underline;
            color: $amarillo;
        }
    }
}