
.boletos {
    padding: $separacion 0;
    color: $white;
    text-align: center;
}

.contenedor-boletos {
    
    @include grid(2, $separacion / 2);

    .basico {
        margin-right: $separacion-s / 1;
    }

    .basico, .premium {

        p{
            padding-top: $separacion-s;
            margin-bottom: 2.2rem;
            color: $white;
            text-align: center;
            font-size: 2.5rem;
        }
    }
}

.pase {
    width: 100%;
    font-size: 2.1rem;
    color: $negro;
    line-height: 2.2;
    margin-bottom: $separacion-s;
    box-shadow: 0px 5px 17px 5px rgba(0,0,0,0.22);

    thead {

        th {
            text-transform: uppercase;
            font-weight: $regular;

        }

        background-color: $rosa;
        color: $white;
        font-size: 2.45rem;
    }

    tbody {
        background-color: $white;

        td {
            padding: $separacion / 5 $separacion-s / 2;
            border-bottom: 2.5px solid #e1e1e1;
        }

        tr {
            &:last-of-type {
                td {
                    border: none;
                }
            }
        }
        
        // margin-bottom: $separacion / 2;
        text-align: left;
        font-weight: $regular;
    }

    tfoot {
        background-color: $morado;
        color: $white;
        font-weight: $regular;
        font-size: 2.8rem;

        &:hover {
            cursor: pointer;    
            transition: all .3s ease;
            @include btn-darker($morado, 10);
        }
    }
}


