
.lineup {
    background-color : $rosa;
    padding : $separacion 0;

    h3, p {
        text-align: center;
        color: $white;
        
    }
    .dia {
        text-transform: uppercase;
        margin: $separacion 0;
    }
}

.escenarios-contenedor {
    @include responsive-tab {
        @include grid(2, $separacion);
    }

    .escenario {
        padding-top: $separacion / 2;
    }

    .nombre-escenario {
        color: $negro;
        font-size: 1.8rem;
        font-weight: $delgada;

        &:after {
            content: '';
            width: 20rem;
            height: 10rem;
            display: block;
            margin: 2rem auto 0 auto;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center;
        }
    }
}

.edm .nombre-escenario {

    &:after {
        background-image: url('/build/img/icono_edm.png')
    }
}

.rock .nombre-escenario {

    &:after {
        background-image: url('/build/img/icono_rock.png');
    }
}

.escenario .calendario {
    padding: 0;
    font-size: 1.5rem;
    list-style: none;

    li {
        padding: $separacion / 2;
        font-weight: $bold;

        span {
            font-weight: $regular;
        }
    }
}

.bg-verde .calendario li:nth-child(odd) {
    background-color: darken($verde, 10);
}

.bg-yellow .calendario li:nth-child(odd) {
    background-color: darken($amarillo, 10);
}