
.contenido-description {
    padding: $separacion-b 0;

    @include responsive-tab {
        @include grid(2, 3.5rem);
    }
}

.contenido-festival {

    display: flex;
    flex-direction: column;
    justify-content: center;

    h2 {
        font-size: 3rem;
    }

    .fecha {
        margin-top: $separacion-s;
        color: $rosa;
        font-weight: $bold;
    }

    .texto {
        font-size: 2.1rem;
        line-height: 1.8;
    }
    
}