
@mixin responsive-mob {
    @media(min-width: $mobile) {
        @content;
    }
}

@mixin responsive-tab {
    @media(min-width: $tablet) {
        @content;
    }
}

@mixin responsive-desk {
    @media(min-width: $desktop) {
        @content;
    }
}

@mixin responsive-mob-m {
    @media(min-width: $mobile) {
        @content;
    }
}

@mixin responsive-tab-m {
    @media(min-width: $tablet) {
        @content;
    }
}

@mixin responsive-desk-m {
    @media(min-width: $desktop) {
        @content;
    }
}

@mixin grid($columnas, $space) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $space;
}

@mixin btn-darker($color, $porcentaje) {

    &:hover {
        background-color: darken($color, $porcentaje);
    }
}